<template>
<div class="content">
    <Header />
    <div class="container">
      <Options 
        v-on:options="optionFilter"
        :disabledState="disabledState"
      />
      <!-- <p v-if="this.error"> {{this.error}} </p> -->
      <div v-if="this.loader" class="filter_container">
        <p class="center">Подождите, загружаем...</p>
      </div>
      <Result 
        :contracts="contracts" 
        :options="options"
      />
    </div>
  </div>
</template>

<script>
import axios from 'axios'

import Header from './components/header'
import Options from './components/options'
import Result from './components/result'
import Contacts from './data/contacts.json'

const mainServerUrl = process.env.VUE_APP_SERVER_URL

export default {
  name: 'App',
  data() {
    return {
      contracts: [],
      options: [],
      loader: false,
      error: '',
      disabledState: 0
    }
  },
  methods: {
    title() {
      document.title = 'Название проекта'
    },
    async optionFilter(options) {
      try {

        this.disabledState = 1;
        this.contracts = [];

        const request = await axios.post(mainServerUrl + 'api/contracts', options);

        this.lodaer = true
        this.options = options;

        this.contracts = request.data.data.contracts.data;
        this.loader = false;

        this.disabledState = 0
      } catch (e) {
        console.log(e);
        this.disabledState = 0;
        alert('Ошибка при запросе');
      }

    },
    async getContact() {
      this.loader = true
      try {
        const data = await axios.get('https://openapi.clearspending.ru/restapi/v3/contracts/top/?year=2021')
      
        // https://openapi.clearspending.ru/restapi/v3/contracts/top/?year=2021
        console.log(data)
        this.contracts = data
        this.loader = false
      } catch(e) {
        this.error = e
        let contacts = JSON.stringify(Contacts)
        let opt =  JSON.parse(contacts)
        this.contracts = opt.contracts.data
        this.loader = false
      }
    },
  },
  mounted() {
    this.title();
  },
  components: { Header, Options, Result, }
}
</script>

<style>
  @import url('styles/css/style.min.css');
</style>